.container {
    width: 85%;
    margin: auto;

}

.navbar_img {
    width: 40px;
    height: 40px;
}

.navbar_div {
    align-items: center;
    display: flex;
    justify-content: space-between;

}

.navbar_ul {
    display: flex;
    list-style: none;
    gap: 40px;
    margin-left: 100px;
}

.navbar_h1 {
    margin-right: 250px;
}

.navbar_icons {
    display: flex;
    gap: 30px;
    padding-right: 50px;
}

.nav_logo {
    display: none;
}

.navbar_ull {
    list-style: none;
    padding: 10px;

}

.navbar_h1_1 {
    text-align: center;
    margin-bottom: 20px;
}

.v {
    margin-bottom: 10px;
    font-size: 20px;
}

.navbar_icons_1 {
    display: flex;
    gap: 30px;
    justify-content: center;
}

.line_1 {
    text-align: center;
    display: none;
    background-color: black;
    color: aliceblue;
}

@media only screen and (max-width:1000px) {
    .nav_logo {
        display: block;
        cursor: pointer;
    }

    .navbar_div {
        display: none;
    }

    .line_1 {
        display: block;

    }

    .navbar_h1 {
        display: none;
    }



    .nav_logo .line {
        width: 30px;
        height: 3px;
        background-color: black;
        margin: 5px 0;
    }

    .line_1 {
        height: 0;
        position: absolute;
        top: -50%;
        left: 0;
        right: 0;
        width: 100vw;
        height: 250px;
        background-color: black;
        color: aliceblue;
        transition: 0.5s;
        overflow: hidden;
    }

    .line_1.active {
        top: 35px;

    }



    .line_1.active ul {
        opacity: 1;
    }

}